.projectCar{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    
    row-gap: 25px;
    /* display: flex;
    flex-wrap: wrap;
    row-gap: 4rem; */
}

@media screen and (max-width:767px) {
    .projectCar{
        grid-template-columns: repeat(2, 1fr);
    }
}


@media screen and (max-width:500px) {
    .projectCar{
        grid-template-columns: repeat(1, 1fr);
      
    }
    .singleText{
        font-size: 13px;
    }
}

